.catalog-nav {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;

    &::before {
      position: absolute;
    }
  }
}

.catalog-nav__link {
  .font(19px; 24px; 700);

  a {
    text-decoration: none;
  }

  span {
    position: relative;
    display: block;
    width: 320px;
    padding: 22px 160px 33px 33px;
    color: #ffffff;
    text-decoration: none;
    box-sizing: border-box;

    @media (max-width: @mobile-width) {
      margin: 0 auto;
    }
  }

  &--interior {
    background-color: @green;

    span::after {
      position: absolute;
      top: 20px;
      right: 40px;
      content: "";
