      background-repeat: no-repeat;
      opacity: 0.5;

      @media (min-width: @tablet-width) {
        top: 30px;
        right: 20px;
      }

      @media (min-width: @desktop-width) {
        right: -170px;
      }
    }
  }

  @media (min-width: @tablet-width) {
    .font (20px; 30px; 700);

    span {
      padding: 26px 160px 35px 35px;
    }
  }
}
