.advantages {
  padding: 30px 0;

  @media (min-width: @tablet-width) {
    padding: 30px 50px;
  }
}

.advantages_wrapper {
  .container();

  @media (min-width: @desktop-width) {
    padding-left: 185px;
    box-sizing: border-box;
  }
}

.advantages__title {
  .font(25px; 30px; 700);
  margin: 0;
  color: #000000;

  @media (min-width: @tablet-width) {
    .font(43px; 50px; 700);
    margin: 27px 0 15px;
  }

  @media (min-width: @desktop-width) {
    .font(53px; 57px; 700);
  }
}

.advantages__list {
  .list-reset();
  width: 250px;

  @media (min-width: @tablet-width) {
    column-count: 2;
    width: auto;
  }

  @media (min-width: @desktop-width) {
    width: 675px;
  }
}

.advantages__item {
  .font(17px; 20px; 700);
  position: relative;
  padding-top: 82px;
  color: #000000;

  @media (min-width: @tablet-width) {
    .font(20px; 22px; 700);
  }

